import React from 'react'
import './TeamSectionCard.css'

function TeamSectionCard() {
    return (
        <div className="TeamSectionCard">
            <img 
            className="TeamSectionCard__image"
            src="data:image/png;base64,"
            alt=""
            />
            <p className="TeamSectionCard__name">LOREM IPSUM</p>
            <p className="TeamSectionCard__devType">Backend<br/>Developer</p>
        </div>
    )
}

export default TeamSectionCard
